











































import Vue from 'vue';
import { apiGetOneBalita, apiUpdateBalita } from '@/services/balita';

type IData = {
  item: {
    nama: string;
    tanggalLahir: string;
    beratBadan: number;
    tinggiBadan: number;
    woman: boolean;
    sex: 'PEREMPUAN' | 'LAKI_LAKI';
  },
  optionsSex: any[]
};

const defData = () => ({
  item: {
    nama: '',
    tanggalLahir: '',
    beratBadan: 0,
    tinggiBadan: 0,
    sex: 'LAKI_LAKI',
    woman: true
  },
  optionsSex: [
    { text: 'Perempuan', value: 'PEREMPUAN' },
    { text: 'Laki - Laki', value: 'LAKI_LAKI' }
  ]
});

export default Vue.extend({

  props: ['id'],

  data: defData,

  methods: {

    async onSave() {
      const payload = {
        ...this.item
      };
      try {
        await apiUpdateBalita(this.id, payload);
        alert('Sukses mengubah data balita');
      } catch (err) {
        alert('Gagal mengubah data balita');
        console.log(err);
      } finally {
        this.$router.back();
      }

    }

  },

  async mounted () {
    this.item = await apiGetOneBalita(this.id);
    this.item.tanggalLahir = this.item.tanggalLahir.substring(0, 10);
  }
});
